.iconImage {
    border-radius: 50%;
}
.textDisplay{
    font-size: 30px;
    display: inline-block;
    margin:15px 30px;
    background-color: white;
    border-radius: 30%;
    border-color:  #a7874b;
    border-style: solid;
    border-width: 10px;
    padding: 20px;
    width: 350px;
    height: 100px;
}
.textResult{
    text-align: center;
}
.descDisplay {
    font-size: 25px;
    display: inline-block;
    background-color: white;
    padding: 20px;
    border-color:  #a7874b;
    border-style: solid;
    border-width: 10px;
    margin:20px auto;
}

.hourIconImage {
    height: 100px;
}

.hourResult {
    display: inline-block;
    margin: 30px 25px;
}
.hourTextDisplay {
    background-color: white;
    border-color:  #a7874b;
    border-style: solid;
    border-width: 5px;
    padding: 5px;
    border-radius: 30%;
}
.addressResult {
    text-align: center;
    font-size: 30px;
    display: inline-block;
    margin:15px 30px;
    background-color: white;
    border-radius: 30%;
    border-color:  #a7874b;
    border-style: solid;
    border-width: 10px;
    padding: 20px;
}